.mini-loaders {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.65);
  top: 0;
  left: 0;
  z-index: 99999;
  text-align: center;
}
.box-mini {
  display: inline-block;
  width: 15px;
  height: 15px;
  font-size: 30px;
  padding: 20px;
  position: relative;
  vertical-align: top;
  transition: 0.3s color, 0.3s border, 0.3s opacity, 0.3s -webkit-transform;
  transition: 0.3s color, 0.3s border, 0.3s transform, 0.3s opacity;
  transition: 0.3s color, 0.3s border, 0.3s transform, 0.3s opacity, 0.3s -webkit-transform;
  margin: 10px auto;
  opacity: 0.9;
}
.loader-time {
  display: inline-block;
  width: 95px;
  height: 95px;
  color: inherit;
  pointer-events: none;
}

.loader-time {
  background: #fff;
  box-shadow: inset 0px 0px 0px 0.1em currentcolor;
  border-radius: 50%;
  position: relative;
  padding: 20px;
}
.loader-time:after,
.loader-time:before {
  position: absolute;
  content: "";
  background-color: currentcolor;
  height: 4px;
  top: 45%;
  left: 50%;
  -webkit-transform-origin: left center;
          transform-origin: left center;
}
.loader-time:after {
  width: 16px;
  -webkit-animation: loader-time 2s linear infinite;
          animation: loader-time 2s linear infinite;
}
.loader-time:before {
  width: 12px;
  -webkit-animation: loader-time 8s linear infinite;
          animation: loader-time 8s linear infinite;
}

@-webkit-keyframes loader-time {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes loader-time {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.react-lock-screen {
  -webkit-filter: none !important;
          filter: none !important;
}

.loader {
  width: 180px;
  height: 180px;
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 50%;
  margin-left: -90px;
  margin-top: -110px;
  border-radius: 20px;
  z-index: 100000;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  text-align: center;
}
.loader.fullScreen {
  position: fixed;
}
.loader.hidden {
  z-index: -1;
  opacity: 0;
  transition: opacity 1s ease 0.5s, z-index 0.1s ease 1.5s;
}


